import { defineComponent, ref, useContext } from '@nuxtjs/composition-api';
export default defineComponent({
    setup() {
        const { app } = useContext();
        const currentLanguage = ref(app.i18n.locale);
        const languages = app.i18n.locales;
        const setLocale = (index) => {
            currentLanguage.value = languages[index].code;
            app.i18n.setLocale(languages[index].code);
        };
        return { currentLanguage, languages, setLocale };
    }
});
